body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.advertisement-image {
  height: 120px;
  width: 100%;
}
.index-channel-site-item {
  width: 100%;
  height: 43px;
  position: relative;
  position: relative;
}
.index-channel-site-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.index-channel-site-item .site-item-text {
  font-weight: 500;
  color: #000;
  font-size: 15px;
  margin-left: 18px;
  line-height: 43px;
}
.index-channel-site-item .icon-keyboard_arrow_right {
  position: absolute;
  right: 10px;
  font-size: 18px;
  line-height: 43px;
  color: #999;
}


