body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.index-box {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 30;
  width: 100%;
  background-color: #fff;
}
.index-box.move-transition {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.index-box.move-enter,
.index-box.move-leave {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.index-box .title {
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  margin: 25px 0 30px;
}
.index-box .icon-arrow_lift {
  position: absolute;
  z-index: 20;
  left: 10px;
  top: 10px;
  color: #999;
  width: 50px;
  height: 50px;
  line-height: 50px;
}
.index-box .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.index-box .tab:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.index-box .tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.index-box .tab .tab-item > a {
  display: block;
  font-size: 15px;
  color: #4d555d;
}
.index-box .tab .tab-item > a.active {
  color: #51a6ff;
  font-weight: 700;
  border-bottom: 2px #51a6ff solid;
}


